

.resumeContainer{
    position: absolute;
    margin-top: 20px;
    margin-left: 61px;
    margin-right: 61px;
    z-index: 10;
    /* left: 149px; */
    /* top: 27px; */
    width: -webkit-fill-available;
    /* height: 500px; */
    
    // border-color: rgba(163, 167, 170, 0.9);
    // border-style: solid;
    // box-shadow: 10px 8px 16px 0px rgba(0, 0, 0, 0.13);
    animation-name: resumeContainer;
    animation-duration: 50000s;
   

}
.scrollContainer{
    height: 80vh;
    overflow: scroll;
    scrollbar-width: none;
    // background-color: white;
}

.resumeHeader{
    position: relative;
    // width: 191vh;
    background-color: #2f2929;
    // top: -22px;
    height: 55px;
    display: flex;
    flex-direction: row-reverse;
    border-color: rgba(163, 167, 170, 0.9);
    border-style: solid;
    border-bottom-color: #f0f8ff00;
}

.exitButton{
    cursor: pointer;
    // background-color: red;
    width: 40px;
    margin-left: 8px;
    margin-right: 8px;
}
.close{
    width: 29px;
    height: 2.3px;
    background:  rgb(163 167 170 / 90%);
    border-radius: 1px;
    top: 25px;
    position: relative;
    right: 1px;
    
      

      &-stem1{
        rotate: 45deg;
      }
      &-stem2{
        rotate: -45deg;
        top: 23px;
      }
}

.resumePic{
    width: -webkit-fill-available;
    // margin-top: 50px;
    margin-bottom: -5px;
}

.downloadIcon{
    cursor: pointer;
    margin: 11px;
    width: 40px;
    height: 29px;
}

@media(max-width:766px){
    
    .resumeContainer{
        margin-top: 20px;
        margin-left: 40px;
        margin-right: 40px;
    }
}
@media(max-width:572px){
    .exitButton{
        margin-right: -2px;
    }
    .close{
        width: 25px;
    }
    .close-stem2{
        top: 23px;
    }
    .downloadIcon{
        width: 34px;
        height: 27px;
    }
    .resumeHeader{
        height: 49px;
    }
}
@media(max-width:450px){
    .exitButton{
        width: 27px;
    }
    .close{
        width: 19px;
        height: 1.5px;
        // right: -21px;
        top: 19px;
    }
    .close-stem2{
        top: 18px;
    }
    .downloadIcon{
        width: 23px;
        height: 19px;
        margin-top: 10px;
        margin-right: 3px;
    }
    .resumeHeader{
        height: 38px;
    }
}
