@keyframes menu-icon {
  0%   {
    margin-top: 5px;
    // margin-bottom: 5px;
  }
  2%   {
    margin-top: 7px;
    // margin-bottom: 7px;
  }
  3%   {
    margin-top: 7.5px;
    // margin-bottom: 7.55px;
  }
  100% {
    margin-top: 7.5px;
    // margin-bottom: 7.5px;
  }
}

@keyframes menu-icon-top {
  0%   {
    margin-top: 4px;
    // margin-bottom: 5px;
  }
  2%   {
    margin-top: 3px;
    // margin-bottom: 7px;
  }
  3%   {
    margin-top: 2.5px;
    // margin-bottom: 7.55px;
  }
  100% {
    margin-top: 2.5px;
    // margin-bottom: 7.5px;
  }
}


@keyframes menu-options {
  0%   {margin-top: -30px;}
  1%   {margin-top: -9px;}
  2%   {margin-top: -5px;}
  3%   {margin-top: -3px;}
  5%   {margin-top: 1px;}
  6%   {margin-top: 2px;}
  7%   {margin-top: 2px;}
  8%   {margin-top: 2px;}
  100% {margin-top: 2px;}
}

.menu-composite{
  display: flex;
      flex-direction: column;
      margin-top: -44px;
      position: absolute;
      right: -15px;
}

.dropdown {
  position: absolute;
  top: 80px;
  right: 51px;
  width: 145px;
  /* max-width: 146px; */
  z-index: 1;
  box-shadow: 10px 8px 16px 0px rgba(0, 0, 0, 0.51);
  border-color: rgba(240, 248, 255, 0.1294117647);
  border-width: 2px;
  border-style: solid;
  border-radius: 2px;
  background-color: rgba(41, 44, 45, 0.9294117647);
  animation-name: menu-options;
  animation-duration: 10s;

    

  &-line{
    width: 91%;
    background-color: #f0f8ff21;
    height: 2px;
    margin-left: 4px;
  }

  &-options{
    text-decoration: none;
    color: white;
    cursor: pointer;
    padding: 12px 16px;
    display: flex;
    align-items: center;
    &:hover{
      background-color: #00000081;
      
    }
  }
}


.menu-icon{
  display: flex;
  flex-direction: column;
  cursor: pointer;
  margin: 32px;
  margin-right: 47px;
  // margin-right: -3px;
  margin-top: 20px;

  // margin-left: 25px;

    &-line{
      width:50px; 
      height:6.5px; 
      margin: 5px;
      background: #000000a8; 

      &-top{
        width:50px; 
      height:6.5px; 
      margin: 5px;
      background: #000000a8;
      }
    }

    

    &:hover{
          
      .menu-icon-line{
        animation-name: menu-icon;
      animation-duration: 10s;
      }

      .menu-icon-line-top{
        animation-name: menu-icon-top;
      animation-duration: 10s;
      }
    }
    
}

.icon-list{
  width: 15px;
  height: 13px;
  margin-right: 5px;
}



@media (max-width:766px){
  .menu-icon{
    
    margin-right: -3px;
  }
  .menu-composite{
    position: relative;
    top: 30px;
    right: 3px;

  }

  .dropdown{
    right: -96px;
    
  }
 
}



@media (max-width:578px){
  .dropdown{
    right: -5px;
    top: 129px;
  }
}





